    const filtered: any = []
    contacts.value.forEach((c: any) => {
      if (!selectedContacts.value.find((e: any) => e.id === c.id)) {
        filtered.push(c)
      }
    })
    return filtered
  },
  set: (data) => (contacts.value = data),
})
