//   expenses.value.filter((f: any) => !service.selectedExpenses.find((e: any) => e.id === f.id))
const _expenses = (service: any) => {
  console.log(service)
  return expenses.value
}
const _vehicles = (service: any) => {
  console.log(service)
  return vehicles.value
}

