const subTotalQuotation: any = computed(() => {
  let total = 0
  selectedServices.value.forEach((c: any) => {
    total = total + c.total
  })
  return total
})
const igvQuotation: any = computed(() => {
  return _.ceil((subTotalQuotation.value * 18) / 100, 2)
})
const totalQuotation: any = computed(() => {
  return _.ceil(+subTotalQuotation.value + +igvQuotation.value, 2)
})

onMounted(async () => {
  loadingData.value = true
  getAllContacts$()
  getAllRoutes$()
  const servicesData: any = await getAllServices()
  const vehiclesData: any = await getAllVehicles()
  const driversData: any = await getAllDrivers()
  const expensesData: any = await getAllExpenses()
  vehicles.value = vehiclesData?.map((i: any) => ({
    ...i,
  }))

  drivers.value = driversData?.map((i: any) => ({
    ...i,
  }))
  services.value = servicesData?.map((i: any) => ({
    ...i,
  }))
  expenses.value = expensesData?.map((i: any) => ({
    ...i,
  }))

  loadingData.value = false

  if (props.data) {
    quotation.value = props.data

    if (props.data.status === 4) {
      steps.value = [{ label: 'Resumen de cotización', icon: 'verified' }]
      step.value = 0
    }

    oportunityStore.getQuotationStates()
    selectedContacts.value = _contacts.value
      .filter((e: any) => e.id === props.data?.customer.id)
      .map((e: any) => ({ ...e, withCustomer: !!props.data?.customer.company }))

    props.data?.services.forEach((e: any) => {
      console.log('SERVICE', e.service.id)
      console.log('___SERVICES', services.value)
      const service = services.value.find((s: any) => s.id === e.service.service)

      console.log('service', e.service)

      const withroute = !!e.service?.route || !!e.service?.address_start
      selectedServices.value = [
        ...selectedServices.value,
        {
          ...service,
          specifications: e.service.specifications,
          original: e.service,
          activetab: 'Servicio',
          activeDetailtab: 'Servicio',
          serviceDetail: {
            passengers: e.service.passengers,
            kilometers: e.service.kilometers,
            withdate: !!e.service.start_date,
            startdate: new Date(e.service.start_date),
            starttime: e.service.start_time && new Date(e.service.start_time),
            enddate: new Date(e.service.end_date),
            endtime: e.service.end_time && new Date(e.service.end_time),
            dateRange: {
              start: new Date(e.service.start_date),
              end: new Date(e.service.end_date),
            },
          },
          selectedExpenses: e.service.expenses.map((data: any) => {
            const expense = expenses.value.find((f: any) => f.id === data.expense)
            return { ...expense, total: data.amount, quantity: data.quantity }
          }),
          selectedVehicles: e.service.vehicles.map((data: any) => {
            const vehicle = vehicles.value.find((f: any) => f.id === data.vehicle)
            return {
              ...vehicle,
              driver: {
                ...data.driver_detail,
                fee_time: data.hours,
                feetotal: data.driverfee_total,
              },
              quantity: data.quantity,
              withDriver: !!data.driver,
              fee_total_perdays: data.total_days,
            }
          }),
          openned: true,
          calculated: null,
          withroute,
          total: _.ceil(e.service.total, 2),
          totalExpenses: _.ceil(e.service.total_expenses, 2),
          tollsamount: _.ceil(e.service.total_tolls, 2),
          tollsPrice: _.ceil(e.service.total_tolls, 2),
          totalVehicles: _.ceil(e.service.total_vehicles, 2),
          _route: e.service.route.id,
          _routetype: e.service.route_type,
        },
      ]
    })

    console.log(selectedServices.value)
  }
})

const getAllRoutes$ = async () => {
  const _routes = await getAllRoutes()
  routes.value = _routes
}
const getAllContacts$ = async () => {
  const contactsData: any = await getAllContacts()
  contacts.value = contactsData?.map((i: any) => ({
    ...i,
    names: i.fullname,
  }))
  _contacts.value = contacts.value
}
const _routeSelected = (id: any) => {
  return routes.value.find((e: any) => e.id === id)
}
const closeViewCustomer = async () => {
  showViewCustomer.value = false
  showNewCustomer.value = false
  await getAllContacts$()
  const items: any[] = []
  selectedContacts.value.forEach((c: any) => {
    const i = contacts.value.find((f: any) => f.id === c.id)
    items.push(i)
  })
  selectedContacts.value = items
}
const addSelected = () => {
  if (selectedCustomer.value) {
    const selected: any = contacts.value.find((f: any) => f.id === selectedCustomer.value)
    selected.withCustomer = !!selected.company
    selectedContacts.value.push(selected)
    // selectedCustomer.value = null
  }
}
const addVehicle = (service: any) => {
  if (service.selectedVehicle) {
    const vehicle = vehicles.value.find((f: any) => f.id === service.selectedVehicle)
    service.selectedVehicles.push({ ...vehicle, driver: { fee: 0, fee_time: 1 }, quantity: 1, withDriver: true })
    service.selectedVehicle = null
    updateTotal(service)
  }
}
const addExpense = (service: any) => {
  if (service.selectedExpense) {
    const expense = expenses.value.find((f: any) => f.id === service.selectedExpense)
    service.selectedExpenses.push({ ...expense, basevalue: expense.value, total: 0, quantity: 1 })
    service.selectedExpense = null
    updateTotal(service)
  }
}
const removeVehicle = (index: number, service: any) => {
  service.selectedVehicles.splice(index, 1)
}
const removeExpense = (index: number, service: any) => {
  service.selectedExpenses.splice(index, 1)
}
const editContact = (contact: any) => {
  contactSelected.value = contact
  showViewCustomer.value = true
}
const removeContact = (index: number) => {
  selectedContacts.value.splice(index, 1)
}
const updateTotal = (service: any) => {
  const startDate = moment(service.serviceDetail.startdate)
  const endDate = moment(service.serviceDetail.enddate)
  const days = endDate.diff(startDate, 'days') + 1
  console.log(startDate, endDate, days)
  //
  service.fee = _.ceil(service?.fee, 2)

  const fee = +service?.fee || 0
  const tollsFrom =
    (service._route ? selectedRoute(service._route)?.amount_from : +service?.serviceDetail?.tollsFrom) || 0
  const tollsTo = (service._route ? selectedRoute(service._route)?.amount_to : +service?.serviceDetail?.tollsTo) || 0
  const tollsAmount =
    (service._route && service._routetype === 1
      ? tollsFrom + tollsTo
      : service._routetype === 1
        ? tollsFrom + tollsTo
        : tollsFrom) || 0
  // Tolls Price
  let tollsPrice = tollsAmount + fee
  let vehiclesPrices = 0
  let expensesPrices = 0
  // Vehicles Price
  const selectedVehicles = service?.selectedVehicles || []
  selectedVehicles.forEach((vehicle: any) => {
    const vehicleFee = +vehicle.fee || 0
    const vehicleFeePerDays = vehicleFee * days
    const quantity = +vehicle.quantity || 0
    const driverFee = +vehicle.driver?.fee || 0
    const driverFeeTime = +vehicle.driver?.fee_time || 0
    const driverFeeTotal = driverFee * driverFeeTime

    vehicle.fee_total_perdays = vehicleFeePerDays

    if (vehicle.driver) {
      vehicle.driver.feetotal = driverFeeTotal
    }

    const total = (vehicleFeePerDays + driverFeeTotal) * quantity
    vehiclesPrices += total
    tollsPrice += total
  })
  const selectedExpenses = service?.selectedExpenses || []
  selectedExpenses.forEach((expense: any) => {
    const value = +expense.value || 0
    const quantity = +expense.quantity || 0
    const total = value * quantity
    expensesPrices += total
  })

  tollsPrice += expensesPrices

  service.tollsamount = _.ceil(tollsAmount, 2)
  service.total = _.ceil(tollsPrice, 2)
  service.totalVehicles = _.ceil(vehiclesPrices, 2)
  service.totalExpenses = _.ceil(expensesPrices, 2)
}
const formatFn = (date: any) => {
  return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`
}
const addSelectedService = (e?: any) => {
  const selected = e ? e.id : selectedService.value
  if (selected) {
    const selectedVehicles: any[] = []
    if (e?.selectedVehicles?.length) {
      e.selectedVehicles.forEach((f: any) => {
        selectedVehicles.push({ ...f })
      })
    }

    const today = new Date()
    today.setHours(0, 0, 0, 0)

    const wDates = {
      withdate: true,
      startdate: today,
      enddate: today,
      dateRange: {
        start: today,
        end: today,
      },
    }

    const newService = {
      ...services.value.find((f: any) => f.id === selected),
      activetab: 'Servicio',
      activeDetailtab: 'Servicio',
      serviceDetail: e ? { ...e.serviceDetail, ...wDates } : { ...wDates },
      selectedExpenses: [],
      selectedVehicles,
      withroute: true,
      withdate: true,
      openned: true,
      calculated: null,
      tollsPrice: null,
      total: 0,
    }
    newService.totalExpenses = 0
    newService.tollsamount = 0
    newService.total = newService.fee
    newService.totalVehicles = 0
    newService.tollsPrice = () => {
      const tollsTo = +newService?.serviceDetail?.tollsTo || 0
      const tollsFrom = +newService?.serviceDetail?.tollsFrom || 0
      return tollsTo + tollsFrom
    }

    selectedServices.value.push(newService)
    selectedService.value = null
  }
}
const driverFee = (driver: number) => drivers.value.find((f: any) => f.id === driver)
const onChangeDriver = (i: any, service: any) => {
  console.log(service)
  if (i.withDriver) {
    const driver = driverFee(i.driver)
    i.driver = { ...driver }
  } else i.driver = null
  updateTotal(service)
}
// const updateDriver = (i: any, service: any) => {
//   i.driver = null
//   updateTotal(service)
// }
const needConfirm = (index: any) => {
  showConfirm.value = index !== null ? true : false
  confirSelected.value = index
}
const doDelete = () => {
  selectedServices.value.splice(confirSelected.value, 1)
  needConfirm(null)
}
const totalFeePerVehicle = (i: any) => {
  // const fee = +i.fee || 0
  const driverFee = +i.driver?.fee || 0
  const driverFeeTime = +i.driver?.fee_time || 0
  const driverFeeTotal = driverFee * driverFeeTime

  if (i.driver) {
    i.driver.feetotal = _.ceil(driverFeeTotal, 2)
  }

  // return fee + driverFee * i.quantity
  return _.ceil(driverFeeTotal + i.fee_total_perdays, 2)
}
const totalFeePerExpense = (i: any) => {
  const fee = i.value || 0
  return fee * i.quantity
}
// const copyOnNew = (index: number) => {
//   const service = selectedServices.value[index]
//   addSelectedService(service)
// }
// const defineRoute = (service: any) => {
//   service._routetype = 1
//   service.serviceDetail.tollsFrom = 0
//   service.serviceDetail.tollsTo = 0
//   if (service.noroute) {
//     service._route = null
//   }
//   updateTotal(service)
// }
const selectedRoute = (id: any) => {
  return routes.value.find((f: any) => f.id === id)
}
const closeProcess = () => {
  confirmProcess.value = false
  errors.value = []
}
const parseFullDate = (date: any, dateTime: any) => {
  if (date && dateTime) {
    const timeHours = dateTime.getHours()
    const timeMinutes = dateTime.getMinutes()
    date.setHours(timeHours)
    date.setMinutes(timeMinutes)
    return date
  }

  return date
}
const doCreate = async () => {
  errors.value = []
  if (quotation.value.title) {
    // errors.value.push('La cotización debe tener un nombre')
  }

  const body: any = {
    services: [],
    title: quotation.value.title,
    total: totalQuotation.value,
    subtotal: subTotalQuotation.value,
    taxes: igvQuotation.value,
  }
  const services: any = selectedServices.value
  const clients: any = selectedContacts.value[0]

  if (!clients) {
    errors.value.push('Debe seleccionar un contacto')
  } else {
    body.customer = {
      id: clients.id,
      ...(clients.withCustomer && { company: clients.company?.id }),
    }
  }
  if (!services.length) errors.value.push('Debes agregar un servicio')

  services.forEach((s: any) => {
    if (!s.withroute) {
      errors.value.push('Debes seleccionar una ruta')
    }

    if (s.withroute && (!s._route || s._routetype === null)) {
      errors.value.push('Los servicios con rutas deben tener un tipo')
    }

    if (s.withroute && s.selectedVehicles.length === 0) {
      errors.value.push('Los servicios con rutas deben tener almenos un vehiculo')
    }

    s.selectedVehicles.forEach((e: any) => {
      if (!e.driver?.id) {
        errors.value.push('Los vehiculos deben tener un tipo de conductor definido')
      }
    })

    const end = parseFullDate(s.serviceDetail.enddate, s.serviceDetail.endtime)
    const start = parseFullDate(s.serviceDetail.startdate, s.serviceDetail.starttime)

    body.services.push({
      service: s.id,
      specifications: s.specifications,
      withroute: s.noroute,
      route: s._route,
      route_type: s._routetype,
      days: s.days,
      kilometers: +s.kilometers,
      passengers: +s.serviceDetail?.passengers,
      tollsFrom: s.serviceDetail?.tollsFrom,
      tollsTo: s.serviceDetail?.tollsTo,
      traveldays: s.serviceDetail?.traveldays,
      // ...(start && { start_date: start }),
      // ...(end && { end_date: end }),
      start_date: start,
      end_date: end,
      endtime: s.serviceDetail.endtime,
      starttime: s.serviceDetail.starttime,
      total: s.total,
      total_tolls: s.tollsamount,
      total_expenses: s.totalExpenses,
      total_vehicles: s.totalVehicles,
      vehicles: s.selectedVehicles?.map((e: any) => ({
        id: e.id,
        driver_type: e.driver?.type,
        driver: e.driver?.id,
        hours: e.driver?.fee_time,
        total_days: e.fee_total_perdays,
        driverfee_total: e.driver?.feetotal,
        driver_fee: e.driver?.fee,
        cost: e.cost,
        capacity: e.capacity,
        fee: e.fee,
        quantity: e.quantity,
      })),
      expenses: s.selectedExpenses?.map((e: any) => ({
        id: e.id,
        fee: e.value,
        name: e.name,
        quantity: e.quantity,
      })),
    })
  })

  if (!errors.value.length) {
    await newOportunity(body)

    notify({
      message: 'Cotización creada correctamente',
      color: 'success',
      duration: 1000,
    })

    emit('close')
  }
}
const onChangeStatus = async (data: any) => {
  oportunityStore.setActiveOportunity(data)
  oportunityStore.viewConfirm()
}
const doNext = (nextStep: any) => {
  nextStep()
}
const closeNewComment = () => {
  showNewComment.value = false
}
const isLoading = ref(false)
const router = useRouter()
const orderStore = useOrdersStore()
const openOrder = async (id: any) => {
  isLoading.value = true

  const order: any = await newOrder({
    id,
  })
  const newOrders = order.news
  const exists = order.exists

  if (exists.length > 0) {
    notify({
      message: 'La cotización tiene ordenes existentes',
      color: 'info',
      duration: 2500,
    })
    orderStore.showOrders(exists)
    isLoading.value = false
  } else {
    if (newOrders.length > 1) {
      notify({
        message: 'Proceso realizado correctamente',
        color: 'success',
        duration: 2500,
      })
      orderStore.showOrders(newOrders)
      isLoading.value = false
    } else {
      notify({
        message: 'Se ha creado la orden #000' + newOrders[0] + '-24',
        color: 'success',
        duration: 2500,
      })
      isLoading.value = false
      router.push('/orders/view/' + newOrders[0])
    }
  }
}
</script>

<template>
  <div class="bg-blue-50 pb-5">
    <div>
      <div class="flex flex-wrap sm:flex-nowrap justify-between align-center justify-center">
        <h1 v-if="!_noedit" class="h5 font-bold m-0">{{ props.data ? 'Editar Cotización' : 'Nueva Cotización' }}</h1>
        <h1 v-if="_noedit" class="h5 font-bold m-0">Detalle de Cotización</h1>
        <div v-if="!_noedit && !loadingData" class="flex gap-2">
          <VaButton v-if="step === 2" icon="save" color="primary" @click="confirmProcess = true">
            <span class="text-xs px-1">{{ props.data ? 'Actualizar Cotización' : 'Crear Cotización' }}</span>
          </VaButton>
        </div>

        <div v-if="props.data && quotation && !loadingData">
          <VaSelect
            v-model="quotation.status"
            :background="quotation.status === 4 ? '#c3ffcb' : quotation.status === 5 ? '#ffa1a1' : '#d5e3ff'"
            :options="statusOptions"
            :readonly="quotation.status === 4"
            value-by="id"
            text-by="label"
            placeholder="---"
            :loading="isOnChangeStatus"
            class="text-xs"
            style="--va-input-wrapper-border-color: transparent; width: 150px"
            @update:modelValue="onChangeStatus(quotation)"
          />

          <VaButton
            v-if="quotation.status === 4"
            icon="library_add"
            class="ml-3 top-0.5"
            color="success"
            :loading="isLoading"
            @click="openOrder(quotation.id)"
          >
            <span class="text-xs px-1">Orden de trabajo</span>
          </VaButton>

          <VaButton
            v-if="quotation.status !== 4"
            icon="add_comment"
            class="ml-3 top-0.5"
            color="info"
            @click="showNewComment = true"
          >
            <span class="text-xs px-1">Registrar interacción</span>
          </VaButton>
        </div>
      </div>

      <div v-if="!loadingData" class="mt-2 bg-white px-2 pt-3 pb-4">
        <VaInput
          v-model="quotation.title"
          background="#d5e3ff"
          name="firstName"
          label="Descripción de cotización"
          autofocus
          :loading="loadingData"
          :readonly="_noedit"
          immediate-validation
          :rules="[validators.required]"
        />
      </div>
    </div>

    <div v-if="!loadingData" class="mt-5 bg-white px-3 py-4">
      <VaTabs v-if="_noedit" v-model="activeDetailTab" center class="mb-7">
        <template #tabs>
          <VaTab v-for="tab in ['Información de Cotización', 'Interacciones']" :key="tab">
            {{ tab }}
          </VaTab>
        </template>
      </VaTabs>

      <VaStepper
        v-if="activeDetailTab === 0 && quotation.status != 4"
        v-model="step"
        class="text-sm new-stepper"
        :steps="steps"
        controls-hidden
      >
        <template #step-content-0>
          <section class="bg-blue-50">
            <div v-if="!props.data" class="p-3">
              <div class="flex justify-between align-content-end">
                <VaSelect
                  v-model="selectedCustomer"
                  class="w-1/3 pt-1 text-xs"
                  background="#fff"
                  :options="_contacts"
                  value-by="id"
                  text-by="fullname"
                  placeholder="---"
                  searchable
                  label="Selecciona o crea un contacto"
                  search-placeholder-text="Buscar contacto"
                  highlight-matched-text
                  :rules="[validators.required]"
                  immediate-validation
                  :disabled="selectedContacts.length"
                >
                  <template #append>
                    <VaIcon name="add_circle" class="ml-2 material-symbols-outlined" @click="addSelected" />
                  </template>
                </VaSelect>

                <VaButton
                  class="ml-3 mr-2 pr-1 pl-1 material-symbols-outlined button-new"
                  icon="person_add"
                  color="primary"
                  :disabled="selectedContacts.length"
                  @click="showNewCustomer = true"
                >
                  <span class="pl-1">Nuevo contacto</span>
                </VaButton>
              </div>
            </div>

            <div v-if="selectedContacts.length" class="p-3 px-4">
              <h3 class="border-b text-xs pb-2 text-black font-bold">Contacto Seleccionado:</h3>

              <VaList class="mt-2">
                <VaListItem
                  v-for="(contact, index) in selectedContacts"
                  :key="index"
                  class="list__item bg-white pl-3 py-1 mb-3"
                >
                  <VaListItemSection>
                    <VaListItemLabel v-if="!props.data" class="font-bold text-xs">
                      <span
                        class="hover:underline cursor-pointer uppercase text-primary"
                        @click="editContact(contact.id)"
                      >
                        {{ contact.fullname }}
                      </span>
                    </VaListItemLabel>

                    <VaListItemLabel v-if="props.data" class="font-bold text-xs">
                      <span class="cursor-pointer uppercase text-primary">
                        {{ contact.fullname }}
                      </span>
                    </VaListItemLabel>

                    <VaListItemLabel caption class="text-xs text-black">
                      <p v-if="!contact.company || (contact.company && !contact.withCustomer)">
                        <span>
                          <span class="font-bold">Documento:</span>
                          {{ contact.document }}
                        </span>

                        <span class="pl-3">
                          <span class="font-bold">Teléfono:</span>
                          {{ contact.cellphone }}
                        </span>
                      </p>

                      <p v-if="contact.company || (contact.company && contact.withCustomer)">
                        <span class="font-bold">Empresa:</span>
                        {{ contact.company?.socialreason }}
                      </p>
                    </VaListItemLabel>
                  </VaListItemSection>

                  <VaListItemSection icon>
                    <VaSwitch
                      v-if="contact.company"
                      v-model="contact.withCustomer"
