                      :disabled="!contact.company"
                      :readonly="props.data"
                      label="Como empresa"
                      size="small"
                    />

                    <VaIcon
                      v-if="!props.data"
                      name="edit"
                      class="material-symbols-outlined cursor-pointer ml-5"
                      @click="editContact(contact.id)"
                    />
                    <VaIcon
                      v-if="!props.data"
                      name="close"
                      class="material-symbols-outlined cursor-pointer"
                      @click="removeContact(index)"
                    />
                  </VaListItemSection>
                </VaListItem>
