import { API } from '../constants'

const services = {
  list: API + '/interactions',
  delete: API + '/interactions',
  detail: API + '/interactions',
  register: API + '/interactions',
  status: API + '/interactions',
}

const _token = localStorage.getItem('__token')

export async function getAllInteractions(quotation: number) {
  const requestOptions = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(`${services.list}/${quotation}`, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

export async function updateInteraction(body: any, id: number) {
  const requestOptions = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
    body: JSON.stringify(body),
  }
  let res = null

  await fetch(`${services.status}/${id}`, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

export async function deleteInteraction(id: any) {
  const requestOptions = {
    method: 'DELETE',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(`${services.delete}/${id}`, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

export async function newInteraction(body: any) {
  const requestOptions = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
    body: JSON.stringify(body),
  }
  let res = null

  await fetch(services.register, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}
